@import 'variable';

body {

  .optimizationWrapper {
    height: 100%;
    h4 {
      padding-bottom: 28px;
    }
    .bottomOptions{
      padding:0px 8px 40px 8px;
      .metricBlk{
        position: relative;
        top: 26px;
      }
    }

    .optimizationRadio{
      padding-top:28px;
      padding-bottom: 0px;
    }
    .generateStation {
      h5 {
        padding-bottom: 12px;
        color: $black;
      }
    }
    .customOptimizationWrapper {
      padding-bottom: 24px;
      cursor: pointer;
      .addCustomOptimization {
        padding: 18px;
        border: 2px solid rgba(0, 0, 0, 0.08);
        box-sizing: border-box;
        border-radius: 16px;
        margin-bottom: 8px;
        .titleBlk{
          margin-bottom: 8px;
        }
        .addCustomOptimizationRow{
          ul{
            li{
              padding: 0 4px;
              &:nth-child(even){
                >div{
                  >div{
                    height: 38px;
                  }
                }
              }
            }
          }
        }
        .buttonTitle {
          color: $black;
        }
        img {
          margin-right: 16px;
        }
        &.active {
          border: 2px solid transparent;
          background-image: linear-gradient(white, white),
            radial-gradient(circle at bottom left, #ef4349, #6a61e7);
          background-clip: padding-box, border-box;
        }
      }
    }
    .optimizationItem {
      padding-bottom: 24px;
      width: 100%;
      display: block;
      .optimizationItems {
        border: 2px solid $gray90;
        border-radius: 16px;
        padding: 20px 20px 16px 20px;
        .imageWrapper {
          padding: 4px 24px 16px 24px;
          max-height: 45px;
        }
        &.active {
          border: 2px solid transparent;
          background-image: linear-gradient(white, white),
          radial-gradient(circle at bottom left, #ef4349, #6a61e7);
          background-clip: padding-box, border-box;
        }
        .bottomText {
          span {
            p {
              display: inline-block;
            }
          }
        }
      }
    }
  }
  .generateRoutesPageWrapper {
    margin-top: 30px;
    margin-bottom: 30px;
    .MuiFormControlLabel-root {
      margin-left: 0;
      margin-right: 0;
    }
    .generateRoutesPage {
      min-height: 578px;
      box-shadow: 0px 8px 22px 6px rgba(87, 96, 117, 0.09);
      border-radius: 10px;
      position: relative;
      .progressSlider {
        position: relative;
        padding-top: 16px;
        padding-bottom: 16px;
        .progressSliderValueLabel {
          position: absolute;
          right: 0px;
          top: 0px;
          color: $black;
        }.progressSliderLabel{
          position: absolute;
          left: 0px;
          top: 0px;
          color: $black;
        }
        &::before {
          content: attr(minval);
          position: absolute;
          left: 6px;
          top: 48px;
          color: rgba(0, 0, 0, 0.54);
          font-size: $font10;
        }

        &::after {
          content: attr(maxval);
          position: absolute;
          right: 0px;
          top: 48px;
          color: rgba(0, 0, 0, 0.54);
          font-size: $font10;
        }
      }

      .bottomBtn {
        width: 100%;
        position: absolute;
        left: 0px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-top: 1px solid $gray90;
        padding: 12px 0;
       

        cursor: pointer;
        img {
          margin-right: 8px;
        }
      }

      .generateRoutesBox {
        background: $secondaryGray;
        border-radius: 10px;
        position: absolute;
        min-height: -webkit-fill-available;
        left: 0px;
        top: 0px;
        bottom: 0px;
        width: 208px;
        .generateRoutesTextWrapper {
          padding: 32px 32px 190px 32px;
        
          .sidebarCoverImg {
            position: absolute;
            left: 0px;
            bottom: 0px;
            width: 100%;
          }
          .stepList {
            color: $white;
            counter-reset: li;
            padding-top: 22px;
            .stepListItem {
              color: rgba(255, 255, 255, 0.5);
              &.active {
                color: $white;
              }
              padding-bottom: 36px;
              span {
                margin-left: 36px;
              }
              position: relative;

              &.active {
                &::before {
                  border: 1px solid $white;
                  color: rgba(0, 0, 0, 0.54);
                  background-color: $white;
                  display: inline-block;
                  inline-size: min-content;
                }
              }
              &::before {
                color: rgba(255, 255, 255, 0.5);
                content: counter(li);
                border: 1px solid rgba(255, 255, 255, 0.5);
                counter-increment: li;
                padding: 2px 8px;
                width: 7px;
                background-color: transparent;
                position: absolute;
                border-radius: 5px;
                top: -2px;
                left: 0px;
              }

              &.completed {
                position: relative;
                &::before {
                  content: url('../img/checkmark_icon.svg');
                  position: absolute;
                  border: none;
                  left: -8px;
                  top: -4px;
                  width: 4px;
                }
              }

              &:last-child {
                &::after {
                  display: none;
                }
              }

              &::after {
                position: absolute;
                left: 11px;
                top: 24px;
                content: '';
                width: 1px;
                height: calc(100% - 30px);
                background: rgba(255, 255, 255, 0.5);
                transform: matrix(-1, 0, 0, 1, 0, 0);
              }
            }
          }
        }
      }
      .generateRoutesSteps {
        padding: 32px 24px;
        position: relative;
        min-height: 578px;
        .sidebarPaddingListWrapper {
          padding-left: 208px;
          padding-right: 276px;
        }
        .sidebarPaddingWrapper {
          padding-left: 208px;
        }
        .detailsNeededWrapper {
          transform: translateY(10px);

          .detailsNeeded {
            border-radius: 5px;
            width: 208px;
            position: relative;
            background: rgba(247, 248, 249, 0.7);
            border: 1px solid rgba(0, 0, 0, 0.08);
            .detailsList {
              li {
                color: rgba(0, 0, 0, 0.54);
              }
            }
            padding: 16px 24px 48px 24px;
            &.noBottomButton {
              padding: 16px 24px 0px 24px;
            }
            h5 {
              padding-bottom: 10px;
              color: $black;
            }

            p {
              padding-bottom: 8px;

              &.optionalField {
                cursor: pointer;
              }

              &.arrow {
                position: relative;
                padding-bottom: 20px;

                &.active {
                  padding-bottom: 12px;

                  &::after {
                    transform: rotate(180deg);
                    position: absolute;
                    left: 58px;
                    top: 3px;
                  }
                }
                &::after {
                  content: url('../img/arrow_down_gray.svg');
                  position: absolute;
                  left: 58px;
                  top: -2px;
                }
              }
            }

            .checkBoxType {
              ul {
                li {
                  position: relative;
                  margin-left: 22px;
                  padding-bottom: 8px;
                  color: $gray40;
                  &::before {
                    content: url('../img/checkbox_nostate.svg');
                    position: absolute;
                    background-color: none;
                    height: 0px;
                    left: -20px;
                    top: 0px;
                  }
                  &.success {
                    &::before {
                      content: url('../img/checbox_green.svg');
                    }
                  }
                  &.error {
                    &::before {
                      content: url('../img/checkbox_red.svg');
                    }
                  }
                }
              }
            }

            ul {
              padding-bottom: 12px;
              li {
               
                color: $gray60;
                position: relative;
                margin-left: 16px;
                padding-bottom: 8px;

                &::before {
                  content: '';
                  position: absolute;
                  background: rgba(0, 0, 0, 0.54);
                  width: 5px;
                  height: 5px;
                  border-radius: 50%;
                  left: -12px;
                  top: 4px;
                }
              }
            }
          }
        }

        .bottomContinueWrapper {
          position: absolute;
          right: 24px;
          bottom: 24px;

          button {
            max-width: 100px;
          }
        }

        .fleetDetailWrapper {
          height: 100%;
          padding-bottom: 12px;
          h4 {
            padding-bottom: 32px;
          }

          .fleetTypeAdd {
            padding-top: 24px;
            h3 {
              padding-bottom: 8px;
              color: $black;
            }
            p {
              color: rgba(0, 0, 0, 0.54);
            }
            .fleetInputGroup {
             
              padding: 24px 0 48px;
              .customQuantityRow{
                position: relative;
                padding-bottom: 16px;
                ul{
                  li{
                    padding: 8px 8px;

                    position: relative;
                    

                    .multiply{
                      position: relative;
                      top: 2px;
                      
                      &.multiplyBlk{
                        top: 38px;
                      }
                    }
                    .closeTextField{
                      position: relative;
                      top: 6px;
                      cursor: pointer;
                    }
                    &:first-child{
                      padding-left: 0;
                    }
                  }
                }
                .addMoreBtnBlk{
                  position: relative;
                  top: 34px;
                }
              }
              .inputLabel {
                padding-bottom: 4px;
                color: $gray60;
              }
              .addMoreButtonWrapper {
                position: relative;
                .addMoreButton {
                  color: rgba(0, 0, 0, 0.54);
                  position: absolute;
                  width: max-content;
                  top: -25px;
                  cursor: pointer;
                  border-radius: 10px;
                  padding: 2px 8px 2px 6px;
                  font-size: 13px;
                  border: 1px solid rgba(0, 0, 0, 0.3);
                }
              }
            }
          }
          .vehicalDetailWrapper {
            padding: 12px 14px;
            border: 2px solid rgba(0, 0, 0, 0.08);
            cursor: pointer;
            border-radius: 8px;
            margin-bottom: 8px;
            .contentBlk{
              position: relative;
             
              .buttonGroup{
                position: absolute;
                bottom: 0px;
                right: 0px;
              }
            }
            .buttonGroup {
              display: flex;
              align-items: center;
              .quantity {
               
                height: 16px;
                width: 10px;
                line-height: 16px;
                position: relative;
                text-align: center;
               
              }
              .smallBtn {
                display: inline-block;
                border-radius: 4px;
                border: 1px solid rgba(0, 0, 0, 0.08);
                width: 15px;
                height: 15px;
               
                color: rgba(0, 0, 0, 0.54);
                position: relative;

                &.plus {
                  &::after {
                    content: '+';
                    position: absolute;
                    left: 3px;
                    bottom: -1px;
                  }
                }

                &.minus {
                  &::after {
                    content: '-';
                    position: absolute;
                    left: 5px;
                    bottom: -1px;
                  }
                }
              }
              .quantity {
                padding: 0 8px;
               
              }
            }
            &.active {
              border: 2px solid transparent;
              background-image: linear-gradient(white, white),
                radial-gradient(circle at bottom left, #ef4349, #6a61e7);
              background-clip: padding-box, border-box;
            }

            .checkboxIcon {
              cursor: pointer;
            }
            .vehicalCoverImageWrapper {
              position: relative;

              &.vehicalQuantityView {
                padding-top: 2px;

                .vehicalCoverImage {
                  min-width: 60px;
                  min-height: 60px;
                }
              }

              .vehicalCoverImage {
                height: 70px;
                object-fit: cover;
                display: inline-flex;
                justify-content: center;
                img{
                  object-fit: contain;
                }
              }
            }
          }
          .fleetSwticher {
            padding-bottom: 24px;
            ul {
              li {
                margin-right: 20px;
               
                &.toggleSwitch{
                  div{
                    label{
                      >div{
                        >div{
                          span{
                            display: none;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }

        
        .rideDetailWrapper {
          height: 100%;
          .fileUploadWrapper {
            height: 100%;
           
            .fileUploadDialogWrapper {
              position: absolute;
              width: 100%;
              height: 100%;
              z-index: 99;
              left: 0px;
              top: 0px;
              .fileUploadDialog {
                width: 100%;
                height: 100%;
                position: absolute;
                z-index: 99;
                left: 0px;
                right: 0px;
                top: 0px;
                bottom: 0px;
             
                display: grid;
                place-items: center;
                text-align: center;
                border-radius: 10px;
                img {
                  margin-bottom: 30px;
                }
                h2 {
                  color: $white;
                }
              }
            }

            .fileUploadDnD {
              height: 500px;
              display: grid;
              place-items: center;
              cursor: pointer;

              ul {
                text-align: center;
                li {
                  position: relative;
                  button {
                    padding: 4px 8px;
                  }

                  h6 {
                    margin-left: 6px;
                    color: $black;
                  }

                  .cloudImage {
                    position: absolute;
                    top: -70px;
                    left: 76px;
                  }
                }
              }
            }
          }
          .tableViewWrapper {
            .fileDetailsWrapper {
              position: absolute;
              right: 24px;
              top: 24px;
              .fileDetails {
                .csvIcon {
                  width: 32px;
                  margin-right: 8px;
                  transform: translateY(4px);
                }
                li {
                  p {
                    margin-left: 16px;
                    position: relative;
                    margin-right: 16px;
             
                    &:before {
                      content: url('../img/clip_icon.svg');
                      position: absolute;
                      left: -18px;
                      top: 2px;
                    }
                  }
                }

                .removeFile {
                  transform: translateY(2px);
                  cursor: pointer;
                }
              }
            }

            .tableView {
              padding-top: 16px;
              padding-bottom: 64px;
              .tableViewContained {
                position: relative;
                overflow: auto;
                max-height: 432px;
                border-radius: 4px;
              }
            }
          }

          .clusterMapViewWrapper {
            h4 {
              padding-bottom: 22px;
            }
            .mapError {
              
              border: 1px solid $primaryRed;
              padding: 16px;
              border-radius: 8px;
              margin-bottom: 16px;
              position: relative;
              .closeIcon {
                position: absolute;
                right: 12px;
                top: 4px;
                cursor: pointer;
              }
              .errorTypeIcon {
                color: $primaryRed;
                margin-right: 8px;
              }
              h5 {
                color: $primaryRed;
              }
              .startOver {
                margin-left: 30px;
                margin-top: 10px;
              }
            }

            .mapBoxViewWrapper {
              height: 480px;
              width: 100%;
              position: relative;
              border-radius: 5px;
              .mapBoxView {
                width: 100%;
                height: 100%;
                border-radius: 5px;
                position: absolute;
              }
              .mapInfo {
                position: absolute;
                top: 10px;
                left: 10px;
                background-color: $white;
                border-radius: 5px;
                box-shadow: 0px 4px 9px 1px rgba(143, 146, 151, 0.19);
                ul {
                  padding: 20px 28px 20px 28px;
                  li {
                    &:first-child {
                      margin-right: 28px;
                    }

                    img {
                      margin-right: 6px;
                    }

                    .mapMarkerIcon {
                      width: 24px;
                    }

                    span {
                      padding-bottom: 4px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  .twoLayout {
    padding-top: 50px;
    display: flex;
    align-items: flex-start;
    .layoutOne {
      width: calc(100% - 286px);
    }
    .layoutTwo {
      padding-left: 30px;
      padding-bottom: 48px;
    }
  }

  .noResultWrapper{
    height: 100%;
    padding-top: 80px;
  }
}

@media (min-width: 1380px) {
  body {
    .generateRoutesPageWrapper {
      padding: 0 108px;
    }
  }
}

@media (min-width: 960px) and (max-width: 1379px) {
  body {
    .generateRoutesPageWrapper {
      padding: 0 54px;
    }
  }
}
