@import "variable";
body {
  .primaryHeader {
    position: fixed;
    right: 0;
    top: 0;
    left: 0;
    background: $white;
    z-index: 999;
    width: 100%;

    box-shadow: 0px 1px 0px #eef1f6;
    .headerWrapper {
      padding: 12px 28px;
      ul {
        padding: 0;
        li {
          &:first-child {
            padding-right: 0px;
          }
          padding: 0 12px;
        }
      }
      .leftBlk {
        ul {
          li {
            .selectField {
              width: 200px;
            }
            .logoBlk {
              .logo {
                height: 32px;
              }
            }
            .selectGroup {
              margin-bottom: 0;
              label {
                display: none;
              }
              .reactSelect {
                min-width: 160px;
                margin-top: -3px;
                > div {
                  height: 32px;
                }
              }
            }
            &:first-child {
              padding-left: 0;
            }
          }
        }
      }
      .rightBlk {
        ul {
          justify-content: flex-end;
          li {
            &:last-child {
              padding-right: 0;
            }
            .generateRouteBtnWrapper {
              height: 40px;
              width: 180px;
              button {
                height: inherit;
                width: inherit;
              }
            }
          }
        }
      }
    }
  }

  .quantityButtonGroup {
    button {
      border: 1px solid $gray60;
    }

    .centerButton {
      min-width: 70px;
    }
  }

  .emptyHeaderdiv {
    height: 62px;
  }
  .__react_component_tooltip {
    padding: 0;
    &.show {
      opacity: 1 !important;
    }
  }
  .tooltip {
    box-shadow: 0px 8px 80px rgba(87, 96, 117, 0.08);
    border-radius: 5px;
    max-width: 300px;
    text-align: left;
    z-index: 999;

    .noShadow {
      box-shadow: none;
    }

    .small {
      box-shadow: none;
      border-radius: 5px;
      padding: 6px;
    }
    .etfTooltip {
      padding: 12px 16px;
      .supportText {
        color: $gray60;
        font-size: $font11;
      }
      .description {
        .supportText {
          padding-bottom: 8px;
        }
        p {
          transform: none !important;
        }
        .paragraph {
          padding-bottom: 8px;
        }
      }

      .etfDetail {
        p {
          transform: none !important;
        }
        .paragraph {
          padding-bottom: 8px;
        }
        ul {
          border: 1px solid $gray90;
          border-radius: 4px;
          display: flex;
          justify-content: space-between;
          li {
            padding: 7px 8px 7px 8px;
            border-right: 1px solid $gray90;
            display: inline-block;

            span {
              display: inline-block;
              margin-left: 4px;
              &.success {
                color: #00a15d;
              }
              &.poor {
                color: $primaryRed;
              }

              &.average {
                color: #ffb114;
              }
            }
          }
        }
      }
    }
  }

  main {
    min-height: calc(100vh - 120px);
    background: $white;
    > div {
      padding: 0px 0px 0px 66px;
      transition: padding 0.3s;
      &.expandedSibarPadding {
        padding: 0px 0px 0px 220px;
        transition: padding 0.5s;
      }
    }
  }

  .rideInfoList {
    margin-top: 8px;
    li {
      span {
        display: inline-block;
        img {
          margin-right: 4px;
          transform: translateY(2px);
        }
      }
      margin-right: 16px;
    }
  }

  .sideBar {
    width: 66px;
    background-color: $secondaryGray;
    position: fixed;
    top: 62px;
    left: 0;
    bottom: 0;
    height: 100%;

    box-sizing: border-box;
    z-index: 998;

    box-shadow: 0px 0px 8px rgba(124, 134, 156, 0.24);
    transition: width 0.3s ease;
    .menuListWrapper {
      padding: 16px 0;
      ul {
        position: relative;
        min-height: calc(100vh - 64px);
        li {
          padding: 0px 0;

          display: block;
          text-align: left;
          a {
            display: block;
            @include transitionForAll($time: 0.4s, $transProp: ease-out);
            border-left: 2px solid transparent;
            padding: 0;
            position: relative;
            height: 56px;
            img {
              margin-left: -2px;
              vertical-align: middle;
              position: absolute;
              left: 22px;
              top: 20px;
            }
            .activeImg {
              display: none;
            }
            p {
              opacity: 0;
              visibility: hidden;
              padding-top: 19px;
              span {
                display: block;
                width: 166px;
                &.menuLabel {
                  font-weight: $boldFont;
                  color: $white;
                  margin-left: 54px;
                }
                &.suggestionLabel {
                  font-weight: $boldFont;
                  color: $secondaryGreen;
                  margin-left: 54px;
                  display: none;
                }
              }
            }
            &:hover {
              @include transitionForAll($time: 0.3s, $transProp: ease-in);
              border-left: 2px solid $primaryRed;
              background: $secondaryGrayDark;
              .activeImg {
                @include transitionForAll($time: 0.3s, $transProp: ease-in);
                display: inline-block;
              }
              .defaultImg {
                display: none;
              }
            }
          }
          &.active {
            a {
              border-left: 2px solid $primaryRed;
              background: $secondaryGrayDark;
              .activeImg {
                display: inline-block;
              }
              .defaultImg {
                display: none;
              }
            }
          }
          &.recommended {
            a {
              &:after {
                content: "";
                position: absolute;
                width: 4px;
                height: 4px;
                background-color: $secondaryGreen;
                top: 44px;
                left: 29px;
                border-radius: 50%;
              }
              p {
                span {
                  &.suggestionLabel {
                    display: block;
                  }
                }
              }
            }
          }
          &.compare {
            a {
              position: absolute;
              bottom: 42px;
              left: 0px;
              width: 66px;
            }
          }
        }
      }
    }
    &:hover {
      &.expandedSibar {
        width: 220px;
        transition: width 0.3s ease;

        .menuListWrapper {
          ul {
            li {
              a {
                p {
                  opacity: 1;
                  visibility: visible;
                }
              }
              &.compare {
                a {
                  width: 218px;
                }
              }
            }
          }
        }
      }
    }
  }

  .logOutDialog {
    padding: 30px 0 24px;
    svg {
      position: absolute;
      cursor: pointer;
      top: 10px;
      right: 10px;
      font-size: 22px;
    }
    h3 {
      margin-bottom: 20px;
    }
    ul {
      text-align: center;
      padding: 8px 0;
      li {
        padding: 0;
        margin-right: 15px;
        button {
          width: 100px !important;
        }
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }

  .generateDialog {
    .dialogTitle {
      padding: 24px 16px 24px 16px;
      text-align: left;
      h3 {
        padding-bottom: 36px;
      }
    }
    .dialogActions {
      padding: 24px 16px;
      .changeBtn {
        button {
          float: right;
        }
      }
    }
  }

  .dilaogParamsActivity {
    .dialogTitle {
      padding: 24px 16px;
      text-align: left;
    }
    .dialogBody {
      padding: 0px 16px 24px 16px;
      text-align: left;
    }
  }

  .currencyDialog {
    .dialogTitle {
      padding: 24px 16px;
      text-align: left;
    }
    .dialogContent {
      margin-bottom: 16px;
      text-align: left;
      ul {
        li {
          padding: 12px;
          .currency {
            display: inline-block;
            cursor: pointer;
            min-width: 170px;
            padding: 16px;
            border: 1px solid $gray95;
            border-radius: 5px;
            @include transitionForAll($time: 0.4s, $transProp: ease-out);
            text-align: left;
            h5 {
              margin-bottom: 4px;
            }
          }
          &:hover {
            .currency {
              background: $background_purple;
              border: 1px solid $secondaryPurple;
              box-shadow: 0px 4px 9px 1px rgba(143, 146, 151, 0.19);
              @include transitionForAll($time: 0.3s, $transProp: ease-in);
            }
          }
          &.active {
            .currency {
              background: $background_purple;
              border: 1px solid $secondaryPurple;
              box-shadow: 0px 4px 9px 1px rgba(143, 146, 151, 0.19);
            }
          }
        }
      }
    }
    .dialogActions {
      padding: 24px 16px;
      .cancelBtn {
      }
      .changeBtn {
        button {
          float: right;
        }
      }
    }
  }

  .noResultFound {
    height: 80%;
    display: grid;
    place-items: center;
    button {
      max-width: 160px;
    }
    .fileName {
      color: #a10e13;
      margin-bottom: 16px;
      img {
        margin-right: 4px;
      }
    }
    .paragraph {
      line-height: 18px;
    }
    h5 {
      margin: 44px 0 16px;
    }
    h6 {
      margin-bottom: 24px;
    }
  }

  .profileDetail {
    position: relative;
    picture {
      margin-right: 8px;
    }

    .userAvatar {
      position: absolute;
      left: 0px;
      top: -2px;
    }
    .userInfo {
      margin-left: 40px;
    }
  }

  .companiesWrapper {
    max-width: 150px !important;
    word-break: break-word;
  }
  .activityLogActions {
    li {
      &:first-child {
        margin-right: 12px;
      }
      &:hover {
        cursor: pointer;
      }
    }
  }

  .profileWrapper {
    button {
      border: none;
      outline: none;
      box-shadow: none;
      background: transparent;
      cursor: pointer;

      .name {
        padding-left: 8px;
        text-transform: capitalize;
        position: relative;
        top: -8px;
        &.email {
          text-transform: unset;
        }
      }
      .caret {
        margin-left: 8px;
        position: relative;
        top: -11px;
      }
      &:hover {
        background: transparent;
      }
    }
    button[aria-controls="menu-list-grow"] .caret {
      transform: rotate(180deg);
    }
    .innerMenuList {
      position: absolute;
      width: 150px;
      right: 22px;
      &.home {
        top: 48px;
      }

      top: 26px;

      ul {
        width: 150px;
      }
    }
  }
  .profileList {
    cursor: pointer;
    li {
      padding: 0 8px;
      text-align: left;
      .profilePic {
        height: 32px;
        width: 32px;
        object-fit: cover;
        border-radius: 50%;
        vertical-align: bottom;
      }
      .name {
        text-transform: capitalize;
        margin-bottom: 4px;
        display: block;
        &.email {
          text-transform: unset;
          margin-top: 0px;
        }
      }
      .caret {
        position: relative;
        top: -4px;
      }
      &:first-child {
        padding-right: 4px;
      }
    }
  }
  .profileDropDown {
    min-width: 160px;
    top: 12px !important;
    z-index: 9;
    > div {
      box-shadow: none;
    }
    ul {
      position: relative;
      background: $white;
      box-shadow: 0px 4px 9px 1px rgba(143, 146, 151, 0.19);
      border-radius: 5px;
      padding: 0;
      li {
        padding: 8px 16px;
        color: $gray60;
        display: block;
        text-align: left;
        cursor: pointer;
        .unit {
          float: right;
          color: $gray40;
        }
        &.logOut {
          border-top: 1px solid $gray95;
        }
      }
    }
  }
  .gooogleMapWrapper {
    height: 100%;
    width: 100%;
    .zoom-custom-control {
      position: absolute;
      right: 10px;
      bottom: 10px;
      z-index: 99;
      cursor: pointer;
    }
    .googleMap {
      height: 100%;
      width: 100%;
      .custom-clustericon {
        background: rgba(106, 97, 231, 0.08);
        border-radius: 100%;
        font-size: 12px;
        display: flex;
        align-items: center;
        color: $secondaryPurple;
        border: 1px solid $secondaryPurple;
      }
    }
  }

  .mapInfoBox {
    padding: 4px 8px;
    opacity: 1;
    border-radius: 5px;
    text-align: left;
    min-width: 100px;
    overflow: hidden;
    display: block;
    border: 1px solid $gray90;
    background-color: $white;

    .walk {
      background-color: $white;
      padding: 4px 0 4px;
      display: block;
      color: $gray70;
      img {
        margin-right: 4px;
      }
    }
  }

  .custom-ganttchart {
    height: calc(100vh - 64px);
    padding-top: 62px;

    padding-bottom: 34px;
    box-sizing: border-box;
    .gantt-panel {
      .gantt-toolbars {
        display: none;
        bottom: -39px;
        width: 100%;
        .gantt-toolbar {
          box-shadow: 11px -8px 20px rgba(87, 96, 117, 0.1);
          padding: 0;
          .toolbar-button {
            padding: 8px 4px 6px;
          }
        }
      }
      .gantt-body {
        .split-pane {
          .left-pane {
            visibility: hidden;
          }
          .split-pane-divider {
            display: none;
          }
          .right-pane {
            left: -6px !important;
            .time-panel {
              .time-line-scroller {
                .vis-timeline {
                  .vis-panel {
                    &.vis-top {
                      background: #ffffff !important;
                      box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.08) !important;
                    }
                    .vis-current-time {
                      display: none !important;
                    }
                    .vis-time-axis {
                      .vis-grid {
                        top: 0 !important;
                      }
                      .vis-text {
                        border-right: 1px dashed #dee2ed !important;
                        @include fontStyle(
                          $size: 12px !important,
                          $weight: $regularFont !important,
                          $lineHeight: 20px !important,
                          $color: $gray40 !important
                        );
                        &.vis-major {
                          border-right: none !important;
                        }
                      }
                    }
                  }
                }
              }
              .time-line-scroller {
              }
              .time-table {
                .time-table-scroller {
                  .time-table-body {
                    .time-table-row-container {
                      .time-table-row {
                        border: 1px dashed #dee2ed;
                        height: 136px !important;

                        .row-number {
                          display: none;
                        }
                        &.highlight,
                        &.selected {
                          background-color: unset;
                        }
                        .time-table-activity {
                          border-radius: 4px;
                          margin-top: 40px !important;
                          .content {
                            .text-content {
                              @include fontStyle(
                                $size: $font10,
                                $weight: $boldFont,
                                $lineHeight: 12px
                              );
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  #profile-menu {
    .MuiPaper-root {
      top: 60px !important;
      left: auto !important;
      right: 8px;
      min-width: 150px;
    }
  }
}
