@import "variable";
body {
  .authPage {
    .loginSection {
      background: $white;
      .leftBlk {
        .introductionWrapper {
          background: linear-gradient(181.6deg, #ebeeff -2.15%, #ffffff 98.72%);
          position: relative;
         
          min-height: 100vh;
          .logo {
            position: absolute;
            top: 28px;
            left: 28px;
            height: 32px;
            z-index: 9;
          }
          .contentBlk {
            padding: 80px 80px 0;
            position: relative;
            z-index: 99;
            h1 {
              margin-bottom: 12px;
              span {
                color: $primaryRed;
              }
            }
            h2 {
              margin-bottom: 24px;
            }
            ul {
              li {
                position: relative;
                padding: 0 0 24px;
                span {
                  display: block;
                  margin-left: 34px;
                }
                &:before {
                  content: "";
                  background: url("../img/red_check.svg") no-repeat;
                  width: 24px;
                  height: 24px;
                  position: absolute;
                  background-size: contain;
                  top: -3px;
                  left: 2px;
                }
              }
            }
          }
          .featuredImgBlk {
            position: absolute;
            bottom: 0px;
            left: 0;
            width: 100%;
            .featuredImg {
              width: 100%;
              position: relative;
              z-index: 9;
         
              object-fit: cover;
              top: 6px;
             
            }
          }
        }
      }
     
      .rightBlk {
        background: $white;
        border-right: 1px solid #eef1f6;
      
        .formWrapper {
          min-height: 100vh;
          position: relative;
          .form {
            padding: 24px;
            display: block;
            width: 100%;
            h3 {
              margin-bottom: 48px;
              text-align: center;
            }
            .submitBlk {
              width: 100%;
              height: 40px;
              button {
                width: inherit;
                height: inherit;
              }
            }
            .copyRights {
              position: absolute;
              bottom: 26px;
              left: 50%;
              transform: translateX(-50%);
              width: 100%;
            
              img {
                margin-right: 5px;
                vertical-align: middle;
                margin-top: -2px;
              }
            }
          }
        }
      }
    }
  }
}

