@import 'variable';
body {
 
  .homePage{
    .landingSection{
      background: linear-gradient(181.6deg, #EBEEFF -2.15%, #FFFFFF 98.72%);
      min-height: 100vh;
      position: relative;
      .logoBlk{
        padding: 28px;
        display: inline-block;
        img{
          height: 32px;
        }
      }
      .contentWrapper{
        position: relative;
        z-index: 99;
        padding: 28px 28px 28px 80px;
        h1{
          margin-bottom: 16px;
        }
        h2{
          margin-bottom: 40px;

        }
        .innerGridWrapper{
          .casestudyList{
            .casestudyItem{
              margin-bottom: 16px;
              a{
                padding: 16px;
                background: $white;
                border: 1px solid $gray95;
                border-radius: 10px;
                display: block;
                position: relative;
                .imgBlk{

                  height: 40px;
                  width: 40px;
                  border-radius: 5px;
                  position: absolute;
                  left: 16px;
                  top: 16px;
                  span{
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%,-50%);
                    color: $gray40;
                  }
                }
                .contentBlk{
                  margin-left: 54px;
                  h3{
                    margin: 2px 0 4px;
                  }
                  h4{
                    color: $gray60;
                  }
                }
              }
            }
          }
          .optionHeading{
             position: relative;
             z-index: 1;
             overflow: hidden;
             text-align: center;
             margin: 24px 0;
             color: $gray90;
             &:before, &:after{
               position: absolute;
              top: 51%;
              overflow: hidden;
              width: 50%;
              height: 1px;
              content: '\a0';
              background-color: $gray90;
             }
             &:before{
                margin-left: -50%;
                text-align: right;
             }
             span{
               padding: 0 10px;
             }
          }
          .generateRoutes{
            width: 100%;
            height: 40px;
            button{
              width: inherit;
              height: inherit;
            }
          }
        }
      }
      .featuredImg{
        position: absolute;
        bottom: 0;
        right: 0;
        max-height: 75%;

      }
    }
  }
  .activtyLog{
    .activityLogSection{
      padding:36px 132px;

      .header{
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        h3{
          padding-bottom: 24px;
          color:$black;
        }
        .selectInputWrapper{
          p{
            color: rgba(0, 0, 0, 0.38);
          }
         .selectInput{
           width: 200px;
           margin-left:8px;
         }

        }
      }

      .userTable{
        padding:24px 0;
      }



    }
  }

  .rightBlock{
    text-align: end;
    .profileDropdown{
      padding: 28px 16px;
      display: flex;
      justify-content: flex-end;
    }
  }
  .shimmerTable{
    .userTable{
      & :global(.profileDetail .profile >div){
        border-radius:50%;
        margin-right: 10px;
      }
    }
  }
}
