@import 'variable';

body {
  .drawerWrapper {
    min-width: 335px;
    overflow-x: hidden;
    box-shadow: 0px 8px 80px rgba(87, 96, 117, 0.16);
    border: 1px solid #eef1f6;
    background-color: $white;
    height: 100%;
    .drawerContainer {
      padding: 20px 16px 28px 28px;
      .complexRouteList {
        padding-top: 20px;
        .complexRouteBox {
          padding: 16px 16px;
          border: 1px solid #eef1f6;
          border-radius: 5px;
          margin-bottom: 46px;
          &:hover {
            cursor: pointer;
            box-shadow: 0px 8px 80px rgba(87, 96, 117, 0.08);
          }
          .rideLocation {
            display: flex;
            align-items: center;
            justify-content: space-between;
            .rideNumber {
              margin-top: 8px;

              img {
                margin-right: 4px;
              }
            }
          }

          .complexRouteBoxInner {
            padding: 36px 0px 36px 16px;
            border-left: 2px dashed $gray90;
            position: relative;
            &.noBorder{
              border-left: 2px dashed transparent;
            }
            margin-left: 44px;

            .topLeftWrapper {
              position: absolute;
              left: -42px;
              top: -6px;
            }
            .bottomLeftWrapper {
              position: absolute;
              left: -42px;
              bottom: -6px;
            }
          }
        }
        .schedule {
          ul {
            display: flex;
          }
          .time {
            position: relative;
            width: 28px;
            span {
              position: absolute;
              left: -10px;
              top: 0px;
            }
          }
          .travelDuration {
            position: relative;
            &:after {
              content: attr(travel);
              position: absolute;
              left: 0px;
              top: 42px;
              font-size: $font10;
              font-weight: $regularFont;
              color: $gray70;
              width: 200%;
            }
          }

          .hollowCircle {
            width: 4px;
            height: 4px;
            background-color: $white;
            border: 4px solid var(--tool-color);
            border-radius: 50%;
            display: inline-block;
            margin-left: 7px;
            margin-right: 7px;
            position: relative;
            z-index: 99;
            &.bottomExtension {
              &::after {
                position: absolute;
                left: 0px;
                top: 8px;
                content: '';
                background-color: var(--tool-color);
                width: 4px;
                height: 76px;
                transform: matrix(-1, 0, 0, 1, 0, 0);
                z-index: 9;
              }
            }
          }
        }
      }
      .routeDetailWrapper {
        p {
          color: $gray70;
          padding-bottom: 12px;
        }
        h6 {
          padding-bottom: 10px;
        }
      }
    }
  }
  .routeEmployeeWrapper {
    padding: 24px 28px;
    min-height: calc(100vh - 112px);
    background-color: $gray95;
    .routeBack {
      color: $primaryRed;
      padding-bottom: 18px;
      transform: translateX(-8px);
      cursor: pointer;
    }
    .leftSideWrapper {
      .headerSidebar {
        margin-right: 18px;
        ul {
          display: flex;
          align-items: center;
          justify-content: space-between;
        }
      }

      .continouesRouteList {
        .rideInfoList {
          z-index: 99 !important;
          .no-click{
            pointer-events: none;
            cursor:not-allowed;
          }
        }
      }
    }
    .rightSideWrapper {
      background-color: $white;
      border-radius: 4px 0px 4px 4px;
      width: 100%;
      height: 100%;
      .rightSideContainer {
        padding: 36px 32px 36px 48px;
        .sidebarHeader {
          padding-bottom: 28px;
          h4 {
            padding-bottom: 8px;
            color: var(--font-color);
          }
        }
        .riderDetailTableWrapper {
          h5 {

            padding-bottom: 18px;
            img {
              margin-right: 8px;
            }
          }
          .searchWrapper {
            width: 300px;
            padding-bottom: 24px;
          }
          .userTable {
            table {
              border-collapse: collapse;
              width: 100%;
              thead {
                border-bottom: 1px solid $gray60;
                th {
                  padding-bottom: 16px;
                  position: relative;
                  &.notSort {
                    &::after {
                      display: none;
                    }
                  }
                  &::after {
                    content: url('../img/sort_icon.svg');
                    position: relative;
                    right: -10px;
                    top: 4px;
                    cursor: pointer;
                  }
                }
              }

              tbody {
                tr {
                  border-bottom: 1px solid $gray95;
                  &:last-child {
                    border-bottom: none;
                  }

                  td {
                    padding-top: 16px;
                    padding-bottom: 16px;
                    color: $gray60;
                    position: relative;
                    picture{
                      margin-right: 8px;
                    }

                    &.address{
                      max-width: 162px;
                      padding-right: 48px;
                    }
                    &.showDetail {
                      padding-bottom: 120px;

                      .userDetail {
                        position: absolute;
                        top: 80px;
                        p {
                          color: $gray40;
                          padding-bottom: 12px;
                        }
                        .tableList {
                          .tableListItem {
                            padding-bottom: 12px;

                            .weekdays {
                              color: $gray90;
                              .active {
                                color: $gray40;
                              }
                            }
                            ul {
                              li {
                                margin-right: 12px;
                              }
                            }
                            &:last-child {
                              padding-bottom: 0px;
                            }
                          }
                        }
                      }
                    }

                    .avatar {
                      > div {
                        margin-right: 12px;
                      }
                    }
                    &.dropdown {
                      .icon {
                        color: $black;
                        margin-left: 40px;
                        cursor: pointer;
                        position: absolute;
                        left: 20px;
                      }
                    }
                  }
                }
              }
              th,
              td {
                text-align: left;
              }
            }
          }
        }
      }
    }
  }
  .caseStudySectionWrapper {
    position: relative;

    .searchBar {
      padding: 20px 24px;
      border-bottom: 1px solid #ebebeb;
    }
    .leftSidebarSection {
      background-color: $white;
      position: absolute;
      z-index: 9;
      box-shadow: 0px 8px 80px rgba(87, 96, 117, 0.16);
      width: 354px;
      height: calc(100vh - 64px);
      left: 0px;
      top: 0px;
      transition: all 1s ease 0s;
      border-right: 1px solid $gray95;
      box-shadow: 0px 8px 80px rgba(87, 96, 117, 0.08);
      &.closeSidebar {
        left: -500px;
        transition: all 0.5s ease 0s;
      }
      .headerBlock {
        padding: 17px 24px 8px 24px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.08);
        ul {
          display: flex;
          justify-content: space-between;
          .leftBlock {
            span {
              display: block;
              font-weight: $boldFont;
              margin-bottom: 5px;
              p {
                color: $black;
                display: inline-block;
                margin-left: 4px;
                cursor: default;
              }
            }
            p {

              cursor: pointer;

              &:hover {
                text-decoration: underline;
              }
            }
          }
          .rightBlock {
            cursor: pointer;
          }
        }
      }
      .headerBlockWrapper{
        .searchWrapper{
          padding: 20px 24px;
          border-bottom: 1px solid rgba(0, 0, 0, 0.08);
          .searchBlk{
            position: relative;
          }
          .closeIcon{
            position: absolute;
            right: 10px;
            cursor: pointer;
            top: 11px;
            font-size: 20px;
            color: #778DA9;
          }
        }
      }
      .allRoutesOuterWrapper {
        padding-top: 0px;
        max-height: calc(100vh - 144px);
        overflow: auto;
        &.hideRoute {
          .innerWrapper {
            visibility: hidden;
          }
        }
        .innerWrapper {
          padding: 20px 0 24px;
          .searchBar {
            padding: 0 24px 20px 24px;
          }
          .closeIcon {
            position: absolute;
            right: 18px;
            top: 84px;
            cursor: pointer;
          }
          .bottomWrapper {
            position: fixed;
            bottom: 0;
            width: 354px;
            padding: 14px 24px;
            box-shadow: 0px 8px 22px 6px rgba(87, 96, 117, 0.09);
            background: $white;
            box-sizing: border-box;
            z-index: 99;
            h3 {
              color: $black;
              span {
                width: 5px;
                height: 5px;
                display: inline-block;
                background: rgba(0, 0, 0, 0.38);
                border-radius: 50%;
                margin: 0 8px;
              }
            }
          }
          .heading5 {
            padding: 0px 24px 8px;
          }
          ul {
            &.allRoutesWrapper {
              li {
                &.eachRouteItem {
                  padding: 18px 24px;
                  background: $white;
                  box-shadow: inset 0px -1px 0px rgba(0, 0, 0, 0.08);
                  cursor: pointer;
                  .RouteNumber {
                    @include fontStyle(

                      $weight: $boldFont,
                      $color: $black,
                      $lineHeight: 13px
                    );
                  }
                  .routeInformation {
                    img {
                      margin-right: 4px;
                      width: 14px;
                    }
                    li {
                      padding-right: 30px;
                      span {
                        @include fontStyle(

                          $size: 13px,
                          $color: $black,
                          $lineHeight: 15px
                        );
                      }
                    }
                  }
                  &.active{
                    background: #f8f7fe;
                  }
                  &:hover {
                    background: #f8f7fe;
                  }
                }
              }
            }
          }
        }
        .performanceInfoBlock {
          padding: 20px 24px;
          position: fixed;
          width: 354px;
          background: $white;
          top: 133px;
          bottom: 0;
          overflow: auto;
          box-sizing: border-box;
          .generateRoute{
            padding-top:36px;
            width: 100%;
            button{
              width: 100%;
            }
          }
          .closeIcon {
            position: absolute;
            right: 18px;
            top: 18px;
            cursor: pointer;
          }
          .sliderWrapper {
            padding-top: 24px;
            &:first-child {
              padding-top: 30px;
            }
            .progressSlider {
              position: relative;
              padding-top: 16px;
              label {
                color: $black;
                font-weight: $regularFont;
              }
              .progressSliderValueLabel {
                position: absolute;
                right: 0px;
                top: 0px;
                p {
                  color: $black;
                }
              }

              .progressSliderLabel {
              position: absolute;
                left: 0px;
                top: 0px;
                p {
                  color: $black;
                }
              }
              &::before {
                content: attr(minval);
                position: absolute;
                left: 0;
                top: 48px;
                color: rgba(0, 0, 0, 0.54);
                font-size: $font10;
                font-weight: $lightFont;
              }

              &::after {
                content: attr(maxval);
                position: absolute;
                right: 0px;
                top: 48px;
                color: rgba(0, 0, 0, 0.54);
                font-size: $font10;
                font-weight: $lightFont;
              }
            }
          }
          .infoBox {
            padding-top: 24px;
            position: relative;
            span {
              .unit {
                color: $black;
                font-weight: $lightFont;
              }
            }
            .currencySymbol {
              position: absolute;
              top: 27px;
              color: $black;
            }
            .mainValueSymbol {
              padding-left: 8px;
              margin-bottom: 4px;
              display: inline-block;
              font-weight: $lightFont;
              color: $black;

              p {
                display: inline-block;
                margin: 0 4px;
              }
            }
            .mainValue {
              margin-bottom: 4px;
              display: inline-block;
              font-weight: $lightFont;
              color: $black;
              p {
                display: inline-block;
                margin: 0 4px;
              }
              position: relative;
              .tag {
                padding: 3px 4px;
                font-size: $font10;
                font-weight: $boldFont;
                background-color: $secondaryGreen;
                color: $white;
                border-radius: 4px;
                position: absolute;
                right: -45px;
                top: 3px;
              }
            }
            .metric {
              display: flex;
              align-items: center;
              color: rgba(0, 0, 0, 0.54);

              img {
                margin-left: 4px;
                cursor: pointer;
              }
            }
          }
        }
      }
    }
    .rightSidebarSectionWrapper {
      padding-left: 354px;
     
      &.full {
        padding-left: 0px;
      }
      .viewSwitcher {
        position: fixed;
        top: 77px;

        right: 55px;
        z-index: 99;
        ul {
          li {
            padding: 8px 16px;
            cursor: pointer;
            background: $white;
            border: 1px solid $primaryRed;
            border-radius: 8px 0 0 8px;
            p {

              img,
              span {
                vertical-align: middle;
              }
              img {
                margin-right: 8px;
                &.active {
                  display: none;
                }
              }
            }
            &.active {
              background: $primaryRed;

              p {
                color: $white;
                img {
                  &.active {
                    display: inline-block;
                  }
                  &.inactive {
                    display: none;
                  }
                }
              }
            }
            &.schedulerBtn {
              border-radius: 0 8px 8px 0;
              p {
                img {
                  position: relative;
                  bottom: 1px;
                }
              }
            }
          }
        }
      }
      .ganttChartWrapper {
        .spinner{

          height: 100vh;
        }
      }
      .rightSidebarSection {
        width: 100%;
        height: calc(100vh - 64px);

      }
    }
  }
  .compareScenarioWrapper {
    padding: 20px 28px;
    .routeBack {
      color: $primaryRed;
      transform: translateX(-8px);
      cursor: pointer;
      padding-bottom: 18px;
    }
    h2 {
      color: $black;
    }

    .viewDetailComparison {
      padding-top: 64px;
      display: flex;
      justify-content: center;
      cursor: pointer;
      svg {
        margin-left: 4px;
      }
      span{
        position: relative;
        top: 2px;
        &.viewMore{
          svg{
            transform: rotate(180deg);
          }
        }
      }
    }

    .overviewList {
      padding-top: 24px;
      .overviewListItemWrapper{
        display: grid;
        /* define the number of grid columns */
        grid-template-columns: repeat( auto-fit, minmax(250px, 1fr) );
        grid-template-rows:  repeat( auto-fit, minmax(260px, 1fr) );
        >div{
          height: 100%;
        }
       
      }
      .overviewItem {
        border-radius: 10px;
        border: 1px solid $gray95;
        padding: 28px 24px 0px 24px;
        height: 100%;
        text-align: center;
        .header {
          h4 {
            padding-bottom: 20px;
          }

          .currency {
            display: inline-block;

            padding-bottom: 16px;

            p {
              display: inline;
              margin-left: 4px;
              color: $primaryRed;
            }
          }
        }
        .bottomWrapper {
          .etf {
            background-color: $gray95;
            border-radius: 5px;
            padding: 10px 16px;


            ul {
              display: flex;
              justify-content: space-between;
              align-items: center;
              li {
                cursor: pointer;
                p {
                  display: inline-block;
                  transform: translate(-6px, 2px);
                }
                img {
                  margin-left: 4px;
                }
              }
            }
          }
          .reasons {
            padding-top: 24px;
            text-align: left;
            list-style: none;
            li {
              color: $gray60;
              padding-bottom: 12px;
              position: relative;
              padding-left: 24px;

              &::before {
                content: url('../img/checbox_green.svg');
                left: 0px;
                top: 0px;
                position: absolute;
              }
            }
          }
        }
      }
    }
  }

  .tableView {
    table {
      border-collapse: collapse;
      width: 100%;
      thead {
        border: 1px solid $gray95;
        th {
          padding: 16px;
          &:first-child {
            border-right: none;
            box-shadow: 0 0 22px rgba(87, 96, 117, 0.1);
            clip-path: inset(0px -22px 0px 0px);
          }
          position: relative;

          border-right: 1px solid $gray95;

          .headerInfo {
            position: relative;
            .tagWrapper {
              position: absolute;
              left: 128px;
              top: -2px;
            }
            .bottomInfo {
              padding-top: 8px;
              ul {
                display: flex;
                justify-content: space-between;
                align-content: center;
                li {
                  .cost {
                    display: inline-block;
                    color: $primaryRed;
                  }
                }
              }
            }
          }
        }
      }

      tbody {
        tr {
          &:last-child {
            border-bottom: none;
          }
          &:nth-child(even) {
            background: rgba(234, 236, 241, 0.4);
          }

          td {
            padding: 20px 0px 24px 20px;
            border-right: 1px solid $gray95;

            position: relative;


            &:first-child {
              border-right: none;

              box-shadow: 0 0 22px rgba(87, 96, 117, 0.1);
              clip-path: inset(0px -22px 0px 0px);
            }
          }
        }
      }
      th,
      td {
        text-align: left;
      }
    }
  }

  .demandMapWrapper{
    position: relative;
    bottom:20px;
    right:20px;
    .demandButton{
      padding:8px 6px;
      cursor: pointer;
      border:1px solid rgba(0, 0, 0, 0.54);
      background: $white;
      border-radius: 5px;
      position: absolute;
      right: 45px;
      bottom: 4px;
      z-index: 999;
      &.active{
        color:$white;
        background: $gray40;
       border:1px solid $gray40;
      }
    }

  }

  .ganttChartShimmerWrapper{
    >div{
      height: 92vh !important;
      width: 100% !important;
      background-size: cover;
      margin: 0 auto;
    }
  }
  .routeEmployeeDetailShimmer{
    table{
      thead{
        th{
          &:after{
            content: none !important;
          }
        }
      }
    }
    .profile{
      >div{
        border-radius: 50%;
        margin-right: 8px;
      }
    }
  }
}
