@import 'variable';

body {
  margin:0px;
  font-family: $font_Roboto;
  .pageWrapper {
    width: 100%;
    height: 100%;
    &.english {
      font-family: $font_Roboto;
    }
    &.arabic {
      font-family: $font_Roboto;
      direction: rtl;
    }
  }

  @include fontStyle(
    $size: $font15,
    $weight: $regularFont,
    $color: $gray40,
   
  );

  * {
    margin: 0;
    padding: 0;
  }

  a {
    text-decoration: none;
    &:hover,
    &:active,
    &:focus,
    &:visited {
      text-decoration: none;
      cursor: pointer;
      outline: 0;
    }
  }

  img {
    max-width: 100%;
  }

  .flexCentered {
    display: flex;
    display: -webkit-flex;
    flex-flow: wrap;
    -webkit-flex-flow: wrap;
    align-items: center;
    &:before,
    &:after {
      width: 0;
    }
  }
  .gridCentered{
    display: grid;
    height: 100%;
    width: 100%;
    place-items: center;
  }
  .disabled{
    pointer-events: none;
    cursor: not-allowed;
  }

  .flexNotCentered {
    display: flex;
    display: -webkit-flex;
    flex-flow: wrap;
    -webkit-flex-flow: wrap;
    &:before,
    &:after {
      width: 0;
    }
  }

  .backgroundProp {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  .hidden {
    display: none !important;
  }



  .white{
    color:$white;
  }
  .red{
    color:$primaryRed
  }

  .customStatus {
    @include fontStyle(
     
      $size: $font10,
      $weight: $regularFont,
      $color: $white,
    
    );
    display: inline-block;
    span {
      padding: 4px 8px;
      border-radius: 5px;
      display: inline-block;
    }
    &.yetToStart {
      span {
        background: $gray40;
      }
    }
    &.inQueue {
      span {
        background: $secondaryBlue;
      }
    }
    &.inProgress {
      span {
        background: $secondaryOrange;
      }
    }
    &.successful {
      span {
        background: $secondaryGreen;
      }
    }
    &.good {
      span {
        background: rgba(0, 161, 93, 0.16);
        border-radius: 3px;
        font-weight: $regularFont;
        color: $secondaryGreen;
      }
    }
    &.average {
      span {
        background: rgba(255, 177, 20, 0.17);
        border-radius: 3px;
        font-weight: $regularFont;
        color: $secondaryOrange;
      }
    }

    &.poor,&.bad {
      span {
        background: rgba(239, 67, 73, 0.16);
        border-radius: 3px;
        font-weight: $regularFont;
        color: $primaryRed;
      }
    }
  }



  .errorMsg {
    position: relative;
    &.absolute{
      position: absolute;
    }
    @include fontStyle(
      $size: $font11,
      $weight: $regularFont,
      $color: $primaryRed,
    
    );
    padding-top: 8px;
    padding-left: 20px;
    &::before {
      content: '';
      background: url('../img/error_icon.svg') no-repeat;
      width: 15px;
      height: 14px;
      position: absolute;
      background-size: contain;
      top: 6px;
      left: 2px;
    }
  }

  .link {
    @include fontStyle(
   
      $weight: $regularFont,
      $color: $primaryRed,
    
    );
  }

  .disabled {
    cursor: not-allowed;
    pointer-events: none;
    opacity: 0.6;
  }

  .listUnstyled {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  .listInline {
    list-style: none;
    &.flex{
      display: flex;
    }
    .listInlineItem {
      display: inline-block;
      width: auto;
      .MuiTouchRipple-root {
        display: none;
      }
    }
  }

  .textCenter {
    text-align: center;
  }

  .textRight {
    text-align: right;
  }

  *:focus {
    outline: 0;
    outline: none;
  }

  .MuiFormLabel-asterisk {
    color: $primaryRed;
  }

  .link {
    text-decoration: none;
    color: inherit;
  }

  .inputGroup {
    outline: none;
    margin-bottom: 24px;

  }

  .passwordGroup {
    position: relative;
    .passwordShow {
      position: absolute;
      top: 36px;
      right: 14px;
      z-index: 99;
      cursor: pointer;
      text-transform: uppercase;
    }
    input {
      padding-right: 55px;
    }
    .MuiTooltip-popper {
      transform: translate3d(0, -136px, 0px) !important;
      right: 0 !important;
      left: auto !important;
    }
  }

  .MuiSelect-select:focus {
    background: $white;
  }

  .MuiTooltip-tooltip {
    padding: 0 !important;
  }
  .MuiTooltip-popper {
    z-index: 99999 !important;
  }

  .moreMenu {
    z-index: 9999 !important;
    .MuiPaper-elevation8 {
      box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06);
    }
  }

  .MuiPopover-root {
    z-index: 99999 !important;
  }

  .MuiContainer-maxWidthLg {
    max-width: 1170px;
  }

  .progressSliderWrapper {
    .sliderLabel {
      span {
        color: $gray40;
        display: inline-block;
        &.minValue {
        }
        &.maxValue {
          float: right;
        }
      }
    }
    .inputGroup {
      margin-bottom: 0;
    }

    &.hideThumb {
      .MuiSlider-root {
        .MuiSlider-thumb {
          display: none;
        }
      }
    }
    .MuiSlider-root {
      color: $gray40;
      height: 3px;
      margin-left: 5px;

      .MuiSlider-rail {
        height: 3px;
        border-radius: 16px;
      }
      .MuiSlider-track {
        height: 3px;
        border-radius: 16px;
        color: $primaryRed;
      }
      .MuiSlider-thumb {
        width: 16px;
        height: 16px;
        color: $white;
        border: 5px solid $white;
        border-radius: 50%;
        box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.15),
          0px 2px 5px rgba(42, 42, 42, 0.15), 0px 1px 1px rgba(0, 0, 0, 0.12);
        margin-top: -7px;
      }
    }

    &.rangeSlider {
      .inputGroup {
        margin-bottom: 24px;
      }
    }
  }


  .customRadioButtonList {
    padding: 0;
    li {
      padding: 0 22px 22px 0;
      a {
        display: inline-block;
      }
      &:last-child {
        padding: 0;
      }
    }
  }

  .customRadioButton {
    a {
      background: transparent;
      border: 1px solid $gray40;
      border-radius: 4px;
      padding: 10px 20px 8px;
      @include transitionForAll($time: 0.3s, $transProp: ease);
    }
    &.active,
    &:hover {
      a {
        border: 1px solid $primaryRed;
        span {
          color: $primaryRed;
        }
      }
    }
    &.multi {
      a {
        padding-right: 40px;
        position: relative;
        span,
        img {
          vertical-align: middle;
        }
        img {
          position: absolute;
          right: 15px;
          width: 11px;
          height: 11px;
          top: 15px;
        }
      }
    }
  }

  .MuiPickersToolbar-toolbar {
    background: $primaryRed;
    button {
      .MuiButton-label {
        .MuiTypography-root {
          @include fontStyle($font_Roboto);
        }
      }
    }
  }
  .MuiPickersBasePicker-pickerView {
    .MuiPickersYearSelection-container,
    .MuiPickersMonthSelection-container {
      .MuiTypography-root {
        @include fontStyle($font_Roboto);
      }
    }
  }
  .MuiPickersMonth-monthSelected,
  .MuiTypography-colorPrimary {
    color: $primaryRed;
  }
  .MuiAvatar-root {
    display: inline-flex;
  }
  .MuiPickersMonth-root:focus,
  .MuiPickersYear-root:focus {
    color: $primaryRed;
  }

  .custom-clustericon,
  .custom-clustericon-1,
  .custom-clustericon-2,
  .custom-clustericon-3 {
    background: rgba(106, 97, 231, 0.08);
    border-radius: 100%;
    font-size: 12px;

    align-items: center;
    color: $secondaryPurple !important;
    > div {
      border: 1px solid $secondaryPurple;
      border-radius: 50%;
      color: $secondaryPurple !important;
    }
  }

}
