$font_Roboto: "Roboto", sans-serif;

$lightFont: 300;
$regularFont: 400;
$boldFont: 700;

$black: #000000;
$white: #ffffff;
$primaryRed: #ef4349;
$secondaryPurple: #6a61e7;
$secondaryBlue: #14c4ff;
$secondaryOrange: #ffb114;
$secondaryGreen: #00a15d;
$secondaryGray: #373d49;
$secondaryGrayDark: #454c5b;

$background_purple: #f3f2fd;

$gray40: #576075;
$gray60: #8a93a8;
$gray70: #a3acc2;
$gray90: #dee2ed;
$gray95: #eef1f6;

$font15: 15px;
$font11: 11px;
$font10: 10px;

@mixin fontStyle(
  $fontFamily: null,
  $size: null,
  $weight: null,
  $color: null,
  $lineHeight: null,
  $margin: null,
  $padding: null
) {
  @if $fontFamily != null {
    font-family: $fontFamily;
  }
  @if $size != null {
    font-size: $size;
  }
  @if $weight != null {
    font-weight: $weight;
  }
  @if $color != null {
    color: $color;
  }
  @if $lineHeight != null {
    line-height: $lineHeight;
  }
  @if $margin != null {
    margin: $margin;
  }
  @if $padding != null {
    padding: $padding;
  }
}

@mixin transitionForAll($time: 0.3s, $transProp: ease) {
  -webkit-transition: all $time $transProp;
  -moz-transition: all $time $transProp;
  -o-transition: all $time $transProp;
  transition: all $time $transProp;
  transition: all $time $transProp;
}

@mixin scrollbars(
  $width,
  $height,
  $border-radius,
  $foreground-color,
  $background-color: mix($foreground-color, white, 50%)
) {
  // For Google Chrome
  &::-webkit-scrollbar {
    width: $width;
    height: $height;
  }

  &::-webkit-scrollbar-thumb {
    background: $foreground-color;
    border-radius: $border-radius;
  }

  &::-webkit-scrollbar-track {
    background: $background-color;
    margin: 20px;
  }

  // For Internet Explorer
  & {
    scrollbar-face-color: $foreground-color;
    scrollbar-track-color: $background-color;
  }
}
